#row {
  display: flex;
  flex-wrap: wrap;
  padding: 0 0px;
  margin: 0 auto;
  margin-left: 22%;
  margin-top: 3%;
  margin-bottom: 3%;
  position: absolute;
}

#column {
  flex: 25%;
  max-width: 8%;
  padding: 0 0px;
}

#column img {
  margin-top: 0px;
  vertical-align: middle;
  width: 100%;
}
